<template>
  <div>
    <select v-model="selectedDataRowType" @change="setDataRowType" class="ds-form-select data-row-type-list">
      <option value="1_Basic_1_Boolean">Basic / Boolean</option>
      <option value="1_Basic_2_Color">Basic / Color</option>
      <option value="1_Basic_3_Date">Basic / Date</option>
      <option value="1_Basic_4_Month">Basic / Month</option>
      <option value="1_Basic_5_Week">Basic / Week</option>
      <option value="1_Basic_6_Number Range">Basic / Number Range</option>
      <option value="1_Basic_7_Word">Basic / Word</option>
      <option value="1_Basic_8_Sentences">Basic / Sentences</option>
      <option value="1_Basic_9_Time">Basic / Time</option>
      <option value="1_Basic_10_Auto-Increment">Basic / Auto-Increment</option>
      <option value="9_KT_1_MDM">KT / MDM</option>
      <option value="9_KT_2_사번">KT / 사번</option>
      <option value="9_KT_3_회사">KT / 회사</option>
      <option value="3_IT_3_Email">IT / Email</option>
      <option value="3_IT_10_User Name">IT / User Name</option>
      <option value="4_Location_6_Phone Number">Location / Phone Number</option>
      <option value="7_Other_3_List">Other / List</option>
    </select>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();

    // default value
    let selectedDataRowType = props.dataRow
      ? computed(
          () =>
            props.dataRow.categoryId +
            '_' +
            props.dataRow.categoryName +
            '_' +
            props.dataRow.item.itemId +
            '_' +
            props.dataRow.item.itemName
        )
      : ref('1_Basic_1_Boolean');

    // dafault dataRowType
    let selectedDataRowTypeSplit = ref(computed(() => selectedDataRowType.value.split('_')));

    // 선택한 data type을 store에 저장
    const setDataRowType = () => {
      store.commit('setDataRowType', {
        index: props.index,
        categoryId: selectedDataRowTypeSplit.value[0],
        categoryName: selectedDataRowTypeSplit.value[1],
        itemId: selectedDataRowTypeSplit.value[2],
        itemName: selectedDataRowTypeSplit.value[3],
      });
      console.log('dataRowType -->', store.getters.getDataRowType(props.index));
    };

    return {
      selectedDataRowType,
      setDataRowType,
    };
  },
});
</script>

<style lang="scss" scoped>
.data-row-type-list {
  appearance: auto !important;
}
</style>
